* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  background-color: #fff;
}

body {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  height: 408px;
  padding: 0 16px 16px;
  transition: transform .3s;
  display: flex;
  position: relative;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  justify-content: center;
  display: flex;
}

.card__image-imac {
  width: 160px;
  height: 134px;
  margin-block: 32px 40px;
}

.card__name {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__name:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.price {
  padding-bottom: 16px;
  justify-content: space-between;
  padding-block: 24px 16px;
  display: flex;
}

.price__name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__costs {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button {
  justify-content: center;
  display: flex;
}

.button__buy {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  align-items: center;
  width: 166px;
  height: 40px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.button__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.stars__list {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__list > .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  font-size: 10px;
  line-height: 14px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  font-size: 0;
  display: block;
}

.nav__list {
  align-items: center;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  white-space: nowrap;
  color: #060b35;
  text-transform: uppercase;
  padding: 23px 0;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link--active {
  color: #00acdc;
  position: relative;
}

.nav__link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

[data-qa="nav-hover"] {
  color: #00acdc;
}

.container {
  grid-gap: 48px 46px;
  grid-column: 4 / 2;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}
